<template>
  <div class="game-details" v-if="game">
    <div class="game-details__header">
      <h1><i class="fas fa-gamepad"></i> Hide & Seek Game Results</h1>
      <div class="game-details__status">
        Game ID: {{ game.gameId }}
        <div class="game-time">Played on: {{ formatDate(game.createdAt) }}</div>
      </div>
    </div>

    <div class="game-result-banner" :class="{ 'winner-p1': isPlayer1Winner, 'winner-p2': isPlayer2Winner }">
      <div class="result-text">
        {{ getWinnerText() }}
        <div class="stake-info"><i class="fas fa-coins"></i> Stake: {{ game.stake }} {{ game.stakeType }}</div>
      </div>
    </div>

    <div class="players-section">
      <div class="player player1" :class="{ 'winner': isPlayer1Winner, 'loser': !isPlayer1Winner && game.winner }">
        <div class="player-header"><i class="fas fa-user-astronaut"></i> <router-link :to="`/player/${game.player1?.userTag}`">{{ game.player1?.userTag || 'Unknown' }}</router-link></div>
        <div class="player-info">
          <div class="moves">
            <div class="move"><i class="fas fa-map-marker-alt"></i> Position: {{ game.player1Position }}</div>
            <div class="move"><i class="fas fa-search"></i> Guess: {{ game.player1Guess !== null ? game.player1Guess : 'Not made' }}</div>
          </div>
        </div>
      </div>
      
      <div class="vs"><i class="fas fa-bolt"></i> VS</div>
      
      <div class="player player2" :class="{ 'winner': isPlayer2Winner, 'loser': !isPlayer2Winner && game.winner }">
        <div class="player-header"><i class="fas fa-user-ninja"></i><router-link :to="`/player/${game.player2?.userTag}`">{{ game.player2?.userTag || 'Unknown' }}</router-link></div>
        <div class="player-info">
          <div class="moves">
            <div class="move"><i class="fas fa-map-marker-alt"></i> Position: {{ game.player2Position }}</div>
            <div class="move"><i class="fas fa-search"></i> Guess: {{ game.player2Guess !== null ? game.player2Guess : 'Not made' }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="game-grid">
      <h3><i class="fas fa-th"></i> Game Board</h3>
      <div class="grid-container">
        <div 
          v-for="i in 4" 
          :key="i"
          :class="[
            'grid-box',
            {
              'p1-position': i === game.player1Position,
              'p2-position': i === game.player2Position,
              'p1-guess': i === game.player1Guess,
              'p2-guess': i === game.player2Guess,
              'winning-move': isWinningMove(i)
            }
          ]"
        >
          <span class="position-number">{{ i }}</span>
          <div class="position-markers">
            <div v-if="i === game.player1Position" class="marker p1-marker"><i class="fas fa-chess-king"></i> P1</div>
            <div v-if="i === game.player2Position" class="marker p2-marker"><i class="fas fa-chess-queen"></i> P2</div>
            <div v-if="i === game.player1Guess" class="marker p1-guess-marker"><i class="fas fa-crosshairs"></i> G1</div>
            <div v-if="i === game.player2Guess" class="marker p2-guess-marker"><i class="fas fa-crosshairs"></i> G2</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div v-else-if="error" class="error-message">
    <i class="fas fa-exclamation-triangle"></i> {{ error }}
  </div>
  <div v-else class="loading">
    <i class="fas fa-spinner fa-spin"></i> Loading game details...
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import axios from 'axios'

const route = useRoute()
const game = ref(null)
const error = ref(null)

const isPlayer1Winner = computed(() => {
  if (!game.value) return false
  return game.value.winner === game.value.player1?.id
})

const isPlayer2Winner = computed(() => {
  if (!game.value) return false
  return game.value.winner === game.value.player2?.id
})

const getWinnerText = () => {
  if (!game.value?.winner) return 'Game ended in a draw'
  const winner = isPlayer1Winner.value ? game.value.player1?.userTag : game.value.player2?.userTag
  return `Winner: ${winner}`
}

const formatDate = (timestamp) => {
  if (!timestamp) return ''
  return new Date(timestamp).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
}

const isWinningMove = (position) => {
  if (!game.value?.winner) return false
  if (isPlayer1Winner.value) {
    return position === game.value.player1Position
  }
  return position === game.value.player2Position
}

const fetchGameDetails = async () => {
  try {
    const response = await axios.get(`/hide-seek-games/${route.params.gameId}`)
    if (response.data.status !== 'finished') {
      error.value = 'This game is not finished yet'
      return
    }
    game.value = response.data
  } catch (err) {
    error.value = 'Failed to load game details'
    console.error('Error fetching game details:', err)
  }
}

onMounted(() => {
  fetchGameDetails()
})
</script>

